import React, {useState, useEffect} from 'react';
import './Navigation.scss';

import NavigationLinks from './NavigationLinks'
import {logo} from '../../images';

function Navbar() {
  const [navMode, setNavMode] = useState(null)

  const toggle = () => setNavMode(navMode !== "develop" ? "develop" : "absolute")

  useEffect(() => {
    window.addEventListener("scroll", () => setNavMode(window.scrollY > 0 ? "" : "absolute"))
}, [])

  return (
    <nav id="Navbar" className={navMode}>
      <img className="Logo" src={logo} alt="Logo"/>
        <ul>
        {NavigationLinks.map((element, i) => 
                <>
                    <li className="contact-info" key={i}>
                        <a className={element.className ? element.className : ""} href={element.link}>{element.title}</a>
                    </li>
                    {element.separator && navMode !== "develop" ? <div className="separator"></div> : null}      
                </>
          )}
        </ul>
        <div className="toggle" onClick={toggle}/>
    </nav>
  );
}

export default Navbar;
