import './About.scss';
import { Section } from '../../dumbComponents'

function About() {
  return (
    <Section id="About" title="A Propos">
        <div className="about-container">
            <p>
            Bienvenue à Grisolles dans le garage Bourgeois,
            vous avez un problème avec votre véhicule ? 
            Nous vous apportons des solutions personnalisées. 
            <br></br>
            <br></br>
            Mécanicien depuis 1992, nous intervenons sur tout types de véhicules automobiles,
            et nous vous proposons des prestations tels que : réparations, 
            préparation au contrôle technique ainsi que des diagnostics électroniques et mécaniques.
            </p>
            <img src="https://www.1001containers.fr/modules/ph_simpleblog/covers/24.jpg" alt="Photo_About_me" />
        </div>
    </Section>
  );
}

export default About;
