import React, { useState } from 'react'
import './Contact.scss';
import { Section, Input, Textarea } from '../../dumbComponents'

import ContactLinks from './ContacLinks';

function Contact() {
    const [name, setName] = useState("")
    const [surname, setSurname] = useState("")
    const [object, setObject] = useState("")
    const [phone, setPhone] = useState("")
    const [message, setMessage] = useState("")

  return (
    <Section id="Contact" title="Contact">

        <div className="contact-container">
            <iframe className="map" title="Google_Maps" src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d1360.5825177758898!2d1.305171233423528!3d43.82290865160432!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x0%3A0x0!2zNDPCsDQ5JzI0LjQiTiAxwrAxOCcyMC43IkU!5e1!3m2!1sfr!2sfr!4v1608755636590!5m2!1sfr!2sfr"/>
            <div className="contact-info-container">
                {ContactLinks.map((element, i) => 
                <>
                    <div className="contact-info" key={i}>
                        <img src={element.icon} alt={element.alt}/>
                        <a href={element.link} target={element.target ? element.target : null}><h3>{element.title}</h3></a>
                    </div>
                    {element.divider ? <div className="divider"></div> : null}      
                </>
                )}
            </div>
        </div>
        
        <form>
            <Input className="classicInput" type="text" value={name} onChange={setName} placeholder="Nom" required/>
            <Input className="classicInput" type="text" value={surname} onChange={setSurname} placeholder="Prénom" required/>
            <Input className="classicInput" type="text" value={object} onChange={setObject} placeholder="Objet de la demande" required/>
            <Input className="classicInput" type="tel" value={phone} onChange={setPhone} placeholder="Téléphone" required/>
            <Textarea className="classicTextarea" value={message} onChange={setMessage} placeholder="Message" required/>
            <a href={"mailto:joelbourgeois51@sfr.fr?subject=" + object + "&body=" + message + "%0A%0AMes informations pour me recontacter :%0ANom : " + name + " " + surname + "%0ATéléphone : " + phone}>Envoyer</a>
        </form>

    </Section>
  );
}

export default Contact;
