import React from 'react';
import './Input.scss'

function Input({ className, type, value, onChange, placeholder, required }) {
  return (
    <input
      className = {className}
      type = {type}
      value = {value}
      onChange = {e => onChange(e.target.value)}
      placeholder = {placeholder}
      required = {required}
    />
  );
}

export default Input;